<template>
    <div>
        <div class="icon-group">
            <a class="icon" href="./#/cart">
                <i class="fas fa-shopping-cart"></i>
                <!-- <span v-if="cartTotalQty > 0" class="cart-badge">{{ cartTotalQty }}</span> -->
            </a>
        </div>
    </div>    
</template>
<style>
.icon-group{
  position: fixed;
  right: 20px;
  bottom: 30px;
  z-index: 200;

  .icon{
    background-color: #fff;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2) ;
    text-decoration: none;

    i{
      color: black;
    }
    }
    
}
.cart-badge {
  position: absolute;
  top: 0;
  right: 0;
  background: red;
  color: white;
  border-radius: 50%;
  padding: 5px 10px;
  font-size: 12px;
}
</style>
<script>

export default {
    name: 'useCart',
    data() {
    return {
      cartTotalQty: 0, // 初始化购物车数量
    };
  },
  methods:{
    getCartTotalQty() {
  const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`;
  this.$http.get(url).then((response) => {
    if (response.data.success) {
      this.cartTotalQty = response.data.data.carts.reduce((total, item) => total + item.qty, 0);
      }
    });
  },
  },
  created() {
      this.getCartTotalQty();
  },
}
</script>