<script>
export default {
    name:'useHander',
    methods: {
    window(){
        window.addEventListener("scroll", function () {
          const header = document.querySelector(".header");
          if (window.scrollY > 0) {
            console.log("123");
              header.classList.add("active");
          } else {
              header.classList.remove("active");
          }
      });
    },
  },
  created(){
    this.window()
  }
}
</script>
